import { useState, useEffect } from 'react';
import { listById } from "./api-quanly";
import { useParams, useNavigate, Navigate } from 'react-router-dom';
import Fraction from 'fraction.js';
import { Button, Dialog, DialogActions, DialogTitle } from '@material-ui/core';
import { removeById } from './api-quanly';
import auth from '../auth/auth-helper';

export default function BieuMauOne() {
    const [values, setValues] = useState([]);
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false); // State to control dialog visibility
    const { id } = useParams();
    const jwt = auth.isAuthenticated();
    const navigate = useNavigate();

    const formattedDates = values.publishDates?.map((date, index, array) => {
        const [year, month, day] = date.split('T')[0].split('-');
        const formattedDate = `${day}/${month}/${year}`;
        return index === array.length - 1 ? formattedDate : formattedDate + ', ';
    });

    const decToFrac = (decimal) => {
        const maxDenominator = 10000;
        const fraction = new Fraction(decimal).toFraction(maxDenominator);
        return fraction.toString();
    }

    const handleEdit = () => {
        navigate('/quanlybieumau/edit/' + id);
    }

    const handleDelete = () => {
        setOpenDeleteDialog(true); // Open the delete confirmation dialog
    };

    const handleCloseDeleteDialog = () => {
        setOpenDeleteDialog(false); // Close the delete confirmation dialog
    };

    const handleDeleteConfirmed = () => {
        const token = jwt.token;
        removeById(id, token).then((data) => {
            if (data.error) {
                console.log(data.error);
            } else {
                navigate('/quanlybieumau');

            }
        }).catch(error => {
            console.error("Error deleting data:", error);
        });
        setOpenDeleteDialog(false); // Close the delete confirmation dialog
    };

    useEffect(() => {
        const token = jwt.token;
        listById(id, token).then((data) => {
            if (data.error) {
                console.log(data.error);
            } else {
                setValues(data);
            }
        }).catch(error => {
            console.error("Error fetching data:", error); // Log any errors that occur
        });
    }, [id])


    if (!jwt){
        return <Navigate to="/dangnhap" />;
    }
    else {
        return (
            <div>
                <p>Phiu yu cu: {values.orderId}</p>
                <p>Tn khch hng: {values.customerName}</p>
                <p>a ch: {values.address}</p>
                <p>Ni dung: {values.content}</p>
                <p>Loi bo: {values.productType}</p>
                <p>Kch thc: {decToFrac(values.size ? values.size.$numberDecimal : 0)}</p>
                <p>Ngy ng: {formattedDates}</p>
                <p>S lng: {values.quantity}</p>
                <p>n gi: {values.pricePerUnit}</p>
                <p>Tng: {values.total}</p>
                <Button variant="contained" onClick={handleEdit} style={{ marginBottom: '10px', marginLeft: '10px' }}>Sa</Button>
                <Button variant="contained" onClick={handleDelete} style={{ marginBottom: '10px', marginLeft: '10px' }}>Xa</Button>



                {/* Delete confirmation dialog */}
                <Dialog open={openDeleteDialog} onClose={handleCloseDeleteDialog}>
                    <DialogTitle>Xc nhn xa</DialogTitle>
                    <DialogActions>
                        <Button onClick={handleCloseDeleteDialog} color="primary">Hy</Button>
                        <Button onClick={handleDeleteConfirmed} color="primary">Xa</Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}
